import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { useQueryParam, StringParam } from 'use-query-params';
import queryString from 'query-string'

const WidgetContainer = styled.div`
  display: block;
  flex: 1;
  justify-content: center;
  align-items: center;
  outline: none;
  min-height: 675px;
  z-index: -1;
`;

const MoonpayWidgetIFrame = styled.iframe`
  height: 100%;
  width: 100%;
  min-height: 425px;
`;

const MoonpayWidget = () => {
  
  // const [currencyCode, setCurrencyCode] = useQueryParam("currencyCode", StringParam);
  // const [walletAddress, setWalletAddress] = useQueryParam("walletAddress", StringParam);
  // console.debug('[MoonpayWidget]: search', currencyCode, walletAddress)
  let moonpaySrcAddress = 'https://buy-staging.moonpay.io';
  const url = queryString(typeof window !== 'undefined' ? window.location.search : '');
  const [apiKey, setApiKey] = useState(url.apikey);
  if (url.currencyCode && url.walletAddress) {
    console.log('[MoonpayWidget]: 3')
    moonpaySrcAddress += '?apiKey=' + url.apiKey + '&colorCode=%23163150'
    moonpaySrcAddress += '&walletAddress=' + url.walletAddress;
    moonpaySrcAddress += '&currencyCode=' + url.currencyCode;
  }

  useEffect(() => {
    if (apiKey) return;
    setApiKey('pk_test_3aM6rCKM7VxglAXwpo6xEm031iQWBbZq')
  }, [])

  console.debug('[MoonpayWidget]: moonpaySrcAddress', moonpaySrcAddress)
  return (
    <WidgetContainer role="group">
      {apiKey && (<MoonpayWidgetIFrame
        allow="accelerometer; autoplay; camera; gyroscope; payment"
        frameBorder={0}
        src={moonpaySrcAddress}
      >
        <p>Your browser does not support iframes.</p>
      </MoonpayWidgetIFrame>)}
      {`search: ${currencyCode} - ${walletAddress}`}
      <br />
      {`moonpaySrcAddress: ${moonpaySrcAddress}`}
    </WidgetContainer>
  );
};

export default MoonpayWidget;
